import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";

import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import S3 from 'react-aws-s3';
import moment from 'moment';
const dotenv = require('dotenv');
//import {CabTypes} from '../CabTypes';
class AddCabs extends Component {

    state = {
        userId: '', item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageNo: 0, payment_orderId: '', carModelName: '', carType: '', carNo: '', rcBook: '',
        extraRate: 0, cabType: '', ac: 'N', bags: '0', capacity: 0, cars: '', rate: 0, returTripRate: 0, discount: 0,cabImage:'',perDayKmReturn:0,perDayKmSingle:0,cabId:0,
        note:'',cabTypes:[],errorListing:''
    };
    constructor(props) {
        super(props);
          
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        this.setState({ userId: userId });

        this.getCabs(userId);
        this.getCabTypes(userId);
    }

    async getCabs(userId) {
        try {
            this.setState({cabId:0})
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId + "&pageId=" + pageNo;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cabs?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                console.log("cabData:"+JSON.stringify(cabData));
                this.setState({ item: cabData.data.data });
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ errorListing: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    async getCabTypes(userId) {
        try {
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId;
            let cabTypesData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cab_types?' + urlData, { headers });
            
            if (cabTypesData.data.code == 200) {
                console.log("CabTypes:"+JSON.stringify(cabTypesData.data.data));
                this.setState({ cabTypes: cabTypesData.data.data });
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });

    }
    async viewCabs(cabId) {
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&cabId=" + cabId;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cabbyid?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                //this.setState({ item: cabData.data.data });
                this.setState({cabType:cabData.data.data.cabType});
                this.setState({ac:cabData.data.data.ac});
                this.setState({bags:cabData.data.data.bags});
                this.setState({note:cabData.data.data.note});
                this.setState({rate:cabData.data.data.rate});
                this.setState({returTripRate:cabData.data.data.returnTripRate});
                this.setState({discount:cabData.data.data.discount});
                this.setState({extraRate:cabData.data.data.extraRate});
                this.setState({perDayKmReturn:cabData.data.data.PerDayKmReturn});
                this.setState({perDayKmSingle:cabData.data.data.PerDayKmSingle});
                this.setState({cabImage:cabData.data.data.image});
                this.setState({capacity:cabData.data.data.capacity});
                this.setState({cars:cabData.data.data.cars});
                this.setState({cabId:cabData.data.data._id});
                
                
            } else {

            }
        } catch (err) {
           
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    addCabs = async () => {
        let token = localStorage.getItem("token");
        
        const headers = { 'Authorization': `Bearer ${token}` };
        
        if (this.state.cabType == "" || this.state.cabType == null) {
            this.setState({ error: "Please enter car number" })
            return false;
        }
        if (this.state.perDayKmReturn == "" || this.state.perDayKmReturn == null) {
            this.setState({ error: "Please enter per day min Km for return trip" })
            return false;
        }
        if (this.state.perDayKmSingle == "" || this.state.perDayKmSingle == null) {
            this.setState({ error: "Please enter per day min Km for single trip" })
            return false;
        }
        if (this.state.rate == "" || this.state.rate == null) {
            this.setState({ error: "Please enter Rate" })
            return false;
        }
        if (this.state.returTripRate == "" || this.state.returTripRate == null) {
            this.setState({ error: "Please enter Return trip rate" })
            return false;
        }
        if (this.state.extraRate == "" || this.state.perDayKmSingle == null) {
            this.setState({ error: "Please enter Extra Km Rate" })
            return false;
        }
        
        let urlData={
            userId:this.state.userId,
            cabType:this.state.cabType,
            ac:this.state.ac,
            bags:this.state.bags,
            capacity:this.state.capacity,
            perDayKmReturn:this.state.perDayKmReturn,
            perDayKmSingle:this.state.perDayKmSingle,
            cars:this.state.cars,
            rate:this.state.rate,
            returTripRate:this.state.returTripRate,
            discount:this.state.discount,
            extraRate:this.state.extraRate,
            cabImage:this.state.cabImage
        }
        try {
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}`,'Content-Type':'application/json' };
            
            let cabData = await axios.post(process.env.REACT_APP_APIURL + 'admin/add_cab' , urlData, { headers });
            
            if (cabData.data.code == 200) {
                //this.setState({ item: cabData.data.data });
                this.setState({ error: 'Cab Added' });
                this.setState({cabType:''});
                this.setState({ac:''});
                this.setState({bags:''});
                this.setState({capacity:''});
                this.setState({perDayKmReturn:0});
                this.setState({perDayKmSingle:0});
                this.setState({cars:''});
                this.setState({rate:0});
                this.setState({returTripRate:0});
                this.setState({discount:0});
                this.setState({extraRate:0});
                this.setState({cabImage:''});
                this.getCabs(this.state.userId);
            } else {
                this.setState({ error: 'Internal error' });
            }
        } catch (err) {
            
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });
        
    }
    
    editCabs = async () => {
        let token = localStorage.getItem("token");
        
        const headers = { 'Authorization': `Bearer ${token}` };
       
        if (this.state.cabId <1 || this.state.cabId == null) {
            this.setState({ error: "Something went wrong" });
            return false;
        }
        if (this.state.cabType == "" || this.state.cabType == null) {
            this.setState({ error: "Please enter car number" })
            return false;
        }
        if (this.state.perDayKmReturn == "" || this.state.perDayKmReturn == null) {
            this.setState({ error: "Please enter per day min Km for return trip" })
            return false;
        }
        if (this.state.perDayKmSingle == "" || this.state.perDayKmSingle == null) {
            this.setState({ error: "Please enter per day min Km for single trip" })
            return false;
        }
        if (this.state.rate == "" || this.state.rate == null) {
            this.setState({ error: "Please enter Rate" })
            return false;
        }
        if (this.state.returTripRate == "" || this.state.returTripRate == null) {
            this.setState({ error: "Please enter Return trip rate" })
            return false;
        }
        if (this.state.extraRate == "" || this.state.perDayKmSingle == null) {
            this.setState({ error: "Please enter Extra Km Rate" })
            return false;
        }
        
        let urlData={
            userId:this.state.userId,
            cabId:this.state.cabId,
            cabType:this.state.cabType,
            ac:this.state.ac,
            bags:this.state.bags,
            capacity:this.state.capacity,
            perDayKmReturn:this.state.perDayKmReturn,
            perDayKmSingle:this.state.perDayKmSingle,
            cars:this.state.cars,
            rate:this.state.rate,
            returTripRate:this.state.returTripRate,
            discount:this.state.discount,
            extraRate:this.state.extraRate,
            cabImage:this.state.cabImage
        }
        try {
            let token = localStorage.getItem("token");
           
            const headers = { 'Authorization': `Bearer ${token}`,'Content-Type':'application/json' };
            
            let cabData = await axios.put(process.env.REACT_APP_APIURL + 'admin/update_cab' , urlData, { headers });
            
            if (cabData.data.code == 200) {
                //this.setState({ item: cabData.data.data });
                this.setState({ error: 'Cab updated' });
                this.setState({cabType:''});
                this.setState({ac:''});
                this.setState({bags:''});
                this.setState({capacity:''});
                this.setState({perDayKmReturn:0});
                this.setState({perDayKmSingle:0});
                this.setState({cars:''});
                this.setState({rate:0});
                this.setState({returTripRate:0});
                this.setState({discount:0});
                this.setState({extraRate:0});
                this.setState({cabImage:''});
                this.getCabs(this.state.userId);
            } else {
                this.setState({ error: 'Internal error' });
            }
        } catch (err) {
            
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });
    }
    onInputHandler(e) {
        const { name, value } = e.target;
        //this.setState({name: value});
    }
    setExtraRate = (rate) => {
        this.setState({ extraRate: rate.target.value });
    }
    setCabType = (cabTypes) => {
        this.setState({ cabType: cabTypes.target.value });
    }
    setAc = (ac) => {
        this.setState({ ac: ac.target.value });
    }
    setBags = (bags) => {
        this.setState({ bags: bags.target.value });
    }
    setCapacity = (capacity) => {
        this.setState({ capacity: capacity.target.value });
    }
    setCars = (cars) => {
        this.setState({ cars: cars.target.value });
    }
    setRate = (rate) => {
        this.setState({ rate: rate.target.value });
    }
    setReturTripRate = (returTripRate) => {
        this.setState({ returTripRate: returTripRate.target.value });
    }
    setDiscount = (discount) => {
        this.setState({ discount: discount.target.value });
    }
    setPerDayKmReturn=(perDayKmReturn)=>{
        this.setState({perDayKmReturn:perDayKmReturn.target.value})
    }
    setPerDayKmSingle=(perDayKmSingle)=>{
        this.setState({perDayKmSingle:perDayKmSingle.target.value})
    }
    deleteCab=async(cabId)=>{
        try {
            this.setState({cabId:0})
            let token = localStorage.getItem("token");
            
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&cabId=" + cabId;
            
            let cabData = await axios.delete(process.env.REACT_APP_APIURL + 'admin/cab?' + urlData, { headers });
            
            if (cabData.data.code == 200) {
                this.setState({ error: 'Cab deleted successfully' });
                this.getCabs(this.state.userId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });
    }
    setCabImage = (event) => {
        let fileData = event.target.files[0];
        let fileSize = fileData.size;
        let fileName = fileData.name;
        let ext = fileName.split('.').pop();
        let fileName_new = moment.now() + "." + ext;
        const env = dotenv.config().parsed;
        

        if (fileSize > 110000) {
            this.setState({ error: 'Image size must be <100kb' });
            this.setState({ rcLink: '' });
            return false;
        }

        this.setState({ isLoading: true });
        
        const config = {
            bucketName: process.env.REACT_APP_BUCKET_NAME,
            dirName: 'cabs', /* optional */
            region: process.env.REACT_APP_REGION,
            accessKeyId: process.env.REACT_APP_S3ACCESS_KEY,
            secretAccessKey: process.env.REACT_APP_S3SECRET_KEY,
        }
        const ReactS3Client = new S3(config);
        const fileUrl = process.env.REACT_APP_CDN_URL + "cabs/" + fileName_new;
        console.log("fileUrl:"+fileUrl);
        ReactS3Client
            .uploadFile(fileData, fileName_new)
            .then(data => {  this.setState({ cabImage: fileUrl }) })
            .catch(err => { this.setState({ error: 'File Not uploaded, Please try again' }); })
        //this.setState({ rcBook: rcBook.target.value });

        this.setState({ isLoading: false });
    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Add Cab Type

                                        </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                {this.state.error}
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Cab Type <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <select className="form-control" value={this.state.cabType} onChange={this.setCabType}>
                                                            <option value="">Select Car Type</option>
                                                            {
                                                                this.state.cabTypes.map((object, i) => {
                                                                    return <option value={object.cabType}>{object.cabType}</option>
                                                                })
                                                            }
                                                        </select>                                                        
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail">
                                                        <Form.Label>AC<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <select className="form-control" value={this.state.ac} onChange={this.setAc}>
                                                            <option value="Y">Yes</option>
                                                            <option value="N">No</option>
                                                        </select>
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Bags <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="bags" type="text" placeholder="Bags" value={this.state.bags} onChange={this.setBags} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Capacity <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="capacity" type="text" placeholder="Capacity" value={this.state.capacity} onChange={this.setCapacity} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Minimum Return Trip Km <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="PerDayKmReturn" type="text" placeholder="PerDayKmReturn" value={this.state.perDayKmReturn} onChange={this.setPerDayKmReturn} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Minimum Oneway Trip Km <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="perDayKmSingle" type="text" placeholder="perDayKmSingle" value={this.state.perDayKmSingle} onChange={this.setPerDayKmSingle} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Cars <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="cars" type="text" placeholder="Cars" value={this.state.cars} onChange={this.setCars} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Rate <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="rate" type="text" placeholder="Rate" value={this.state.rate} onChange={this.setRate} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Return Trip Rate <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="returTripRate" type="text" placeholder="Return trip rate" value={this.state.returTripRate} onChange={this.setReturTripRate} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Discount <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="discount" type="text" placeholder="Discount" value={this.state.discount} onChange={this.setDiscount} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Extra Rate <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="extraRate" type="text" placeholder="Car Model Name`" value={this.state.extraRate} onChange={this.setExtraRate} />
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Upload Image<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control type="file" onChange={this.setCabImage} placeholder="Select RC" />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12">
                                                {this.state.cabId<1?<Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.addCabs.bind(this)}>
                                                        Add Cab type
                                                    </Button>
                                                </Form.Group>:<Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.editCabs.bind(this)}>
                                                        Update Cab type
                                                    </Button>
                                                </Form.Group>
                                                }
                                                
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>
                            <br />
                            <div className="row">
                                <div className="col-lg-12 col-md-12">
                                    
                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Cabs </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <div>{this.state.errorListing}</div>
                                                <Table striped bordered hover responsive>
                                                    <thead>
                                                        <tr>
                                                            <th>Type</th>
                                                            <th>Rate</th>
                                                            <th>Return TripRate</th>
                                                            <th>Discount</th>
                                                            <th>ExtraRate</th>
                                                            <th>PerDayKmReturn</th>
                                                            <th>PerDayKmSingle</th>
                                                            <th>Ac </th>
                                                            <th>Bags</th>
                                                            <th>Capacity</th>
                                                            <th>Action</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.item.map((object, i) => {
                                                                return <tr key={i}>
                                                                    <td>{object.cabType}</td>
                                                                    <td>{object.rate}</td>
                                                                    <td>{object.returnTripRate}</td>
                                                                    <td>{object.discount}</td>
                                                                    <td>{object.extraRate}</td>
                                                                    <td>{object.PerDayKmReturn}</td>
                                                                    <td>{object.PerDayKmSingle}</td>
                                                                    <td>{object.ac}</td>
                                                                    <td>{object.bags}</td>
                                                                    <td>{object.capacity}</td>
                                                                    <td>
                                                                        <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.viewCabs.bind(this,object._id)}>
                                                                            <i className="fa fa-edit iconStyle" aria-hidden="true"></i>
                                                                        </Button>&nbsp;&nbsp;
                                                                        <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.deleteCab.bind(this,object._id)}>
                                                                            <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                        </Button>
                                                                        
                                                                    </td>
                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>

                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(AddCabs);